import React, { Component } from 'react';
import { Row, Col, Form, DatePicker, Select } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getPaymentTypes } from 'services/receiptsAndExpensesStatistic';
import { requiredRule } from 'utils/validateRules';
import './style.scss';

const { Option } = Select;

@Form.create({ name: 'FormSearchClasses' })
class FormSearchStudent extends Component {
  state = {
    listPaymentTypes: [],
  };

  componentDidMount = () => {
    const { form } = this.props;
    const paramSearch = getQueryParams();
    delete paramSearch._time;
    form.setFieldsValue(paramSearch);
    this.getDataPaymentTypes();
  };

  getDataPaymentTypes = () => {
    getPaymentTypes().then(res => {
      console.log('res.data.items', res.data.items);
      this.setState({
        listPaymentTypes: res.data.items,
      });
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      updateQueryParams(values);
    });
  };

  render() {
    const { listPaymentTypes } = this.state;
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    return (
      <Form
        className="form-inline-label search-form"
        layout="vertical"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24} type="flex" align="stretch">
          <FilterPathEducate
            form={this.props.form}
            optionFieldDecorator={{
              trainTypeId: {
                rules: [requiredRule],
              },
              facultyId: {
                rules: [requiredRule],
              },
              courseId: {
                rules: [requiredRule],
              },
            }}
          />
          <Col span={5}>
            <Form.Item label="Năm học">
              {getFieldDecorator('yearEduId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('semesterId', { rules: [requiredRule] })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Loại thu">
              {getFieldDecorator('paymentTypeId', {})(
                <Select showSearch>
                  {listPaymentTypes.map(item => (
                    <Option key={item.value}>{item.label}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Từ ngày">
              {getFieldDecorator('fromDate', {
                rules: [{ type: 'object' }],
              })(<DatePicker placeholder="Chọn ngày" />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Đến ngày">
              {getFieldDecorator('toDate', {
                rules: [{ type: 'object' }],
              })(<DatePicker placeholder="Chọn ngày" />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



